<template>
    <div>
       <div class=" card card-body m-3">
        <h5>{{article.title}}</h5>
        <div><span class="badge badge-secondary mr-1">{{article.createTime}}</span></div>
        <div><span class="badge badge-secondary mr-1" v-for="(badge, index) in article.badges" :key="index">{{badge.text}}</span></div>
        <div>
        <p class=" text-left m-2" v-html="article.context"></p>
      </div>
    </div>
    <div class=" p-3">
      <related-article :tags="article.badges"></related-article>
      <recommond-article></recommond-article>
    </div>
    <comment dataId='test'></comment>
    </div>
</template>
<script>
import recommondArticle from './recommondArticle'
import relatedArticle from './relatedArticle'
import comment from './common/commentLiveRe.vue'
import navigator from './common/navigator'
export default {
  data () {
    return {
      article: {
        title: '',
        updateTime: '',
        createTime: '',
        badges: [],
        context: ''
      }
    }
  },
  created () {
    if (this.$route.params === undefined || this.$route.params.id === undefined) {
      this.$router.push({path: '/error', query: { type: '404' }})
    } else {
      var articles = JSON.parse(localStorage.getItem('articles'))
      var count = 0
      var VueObj = this
      articles.forEach(function (item) {
        if (item.id === VueObj.$route.params.id) {
          VueObj.article.title = item.name
          VueObj.article.badges = item.tags
          VueObj.article.updateTime = item.updateTime
          VueObj.article.createTime = item.createTime
          VueObj.$projectApi.getArticle(VueObj.$route.params.id, function (data) {
            VueObj.article.context = data
          })
          count++
          return false
        }
      })
      if (count <= 0) {
        VueObj.$router.push({path: '/error', query: { type: '404' }})
      }
    }
  },
  components: {navigator: navigator, comment, relatedArticle, recommondArticle}
}
</script>
<style>

</style>



