<template>
    <div class=" card card-body float-left w-50">
        <h4 class="align-self-start">{{$t("recommond_article")}}</h4>
        <ul class=" list-group">
            <li class=" list-group-item" v-for="(article, index) in recommondArticles" :key="index" style="height:5rem;">
                <h5 class="align-self-start d-flex">
                    <router-link :to="'/article/'+article.code" target="_blank">{{article.title}}</router-link>
                    </h5>
                <p v-html="article.description" class="text-truncate"></p>
            </li>
        </ul>
    </div>
</template>
<script>
export default {
  data () {
    return {
      recommondArticles: []
    }
  },
  created () {
    this.recommondArticles = JSON.parse(
      localStorage.getItem('recommend_article')
    )
    var allArticles = JSON.parse(localStorage.getItem('articles'))
    var VueObj = this
    this.recommondArticles.forEach(function (recommend, index) {
      allArticles.forEach(function (article) {
        if (recommend.code === article.id) {
          recommend.title = article.name
          recommend.time = article.createTime
          recommend.tags = article.tags
          VueObj.$set(VueObj.recommondArticles, index, recommend)
        }
      })
    })
  },
  props: {}
}
</script>



