<template>
    <div class="mr-4 ml-4">
      <div v-for="(article, index) in articles" :key="index" class="float-left">
        <article-preview :article="article"></article-preview>
      </div>
    </div>
</template>
<script>
import articlePreview from './articlePreview'
export default {
  data () {
    return { articles: [] }
  },
  created () {
    this.articles = JSON.parse(localStorage.getItem('recommend_article'))
    var allArticles = JSON.parse(localStorage.getItem('articles'))
    var VueObj = this
    this.articles.forEach(function (recommend, index) {
      allArticles.forEach(function (article) {
        if (recommend.code === article.id) {
          recommend.title = article.name
          recommend.time = article.createTime
          recommend.tags = article.tags
          VueObj.$set(VueObj.articles, index, recommend)
        }
      })
    })
  },
  components: { 'article-preview': articlePreview }
}
</script>



