<template>
    <div class=" card card-body float-left w-50">
        <h4 class=" align-self-start">{{$t("related_article")}}</h4>
        <ul class=" list-group">
            <li class=" list-group-item" v-for="(article, index) in relatedArticles" :key="index" style="height:5rem;">
                <h5 class="align-self-start d-flex">
                    <router-link :to="'/article/'+article.id" target="_blank">{{article.name}}</router-link>
                </h5>
                <p v-html="article.description" class="text-truncate"></p>
            </li>
        </ul>
    </div>
</template>
<script>
export default {
  data () {
    return {
      relatedArticles: []
    }
  },
  created () {
    // var recommondArticles = JSON.parse(
    //   localStorage.getItem('recommend_article')
    // )
    var allArticles = JSON.parse(localStorage.getItem('articles'))
    var tags = this.tags
    var VueObj = this
    allArticles.forEach(function (article) {
      var count = 0
      article.tags.forEach(function (tag) {
        tags.forEach(function (tagParam) {
          if (tagParam.code === tag.code) {
            count += 1
          }
        })
      })
      if (count > 0) {
        VueObj.relatedArticles.push(article)
      }
    })
  },
  props: {
    tags: {
      type: Array,
      required: true
    }
  }
}
</script>
<style>
</style>



