<template>
    <div>
        <nav class="navbar navbar-expand-lg navbar-light bg-light position-fixed w-100"  style="z-index:999;">
            <div class="navbar-header">
                <router-link class="navbar-brand" to="/"><img style="width: 5rem;" src="./../../../static/img/brand.png"></router-link>
                <button class="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarToggleExternalContent" aria-controls="navbarToggleExternalContent" aria-expanded="false" aria-label="Toggle navigation">
                    <span class="navbar-toggler-icon"></span>
                </button>
            </div>
            <div class="collapse navbar-collapse" id="navbarToggleExternalContent">
                <ul class="navbar-nav mr-auto">
                    <li class="nav-item active" v-for="(nav, index) in navs" v-bind:key="index">
                        <router-link class="nav-link" to="/homePage">{{ $t('home') }}</router-link>
                    </li>
                </ul>
            </div>
            <form class="form-inline">
                <ul class="list-inline align-items-baseline mb-0 border-right">
                    <li class="list-inline-item">
                        <i class="iconfont icon-search" @click="searchPage('search-article')"></i>
                    </li>
                    <li class="list-inline-item">
                        <i class="iconfont icon-setting" @click="settings()"></i>
                    </li>
                    <li class="list-inline-item mr-3">
                        <i class="iconfont icon-xiaoxi1" @click="message()"></i>
                    </li>
                </ul>
                <img class=" rounded-circle align-items-baseline ml-3" @click="openMyPage()" style="height:2.5rem;" src="./../../../static/img/myIcon.png">
            </form>
        </nav>
        <my-page ref="myPage"></my-page>
        <search-article ref="searchArticle"></search-article>
        <settings ref="settings"></settings>
    </div>
</template>
<script>
import myPage from './../myPage'
import searchArticle from './../searchArticle'
import settings from './../settings'
export default {
  date () {
    return { navs: [] }
  },
  components: { 'my-page': myPage, 'search-article': searchArticle, settings },
  created () {
    this.navs = [{ text: 'Home', code: 'home-page' }]
  },
  methods: {
    searchPage (pageName) {
      this.$refs.searchArticle.display()
    },
    openMyPage () {
      this.$refs.myPage.display()
    },
    settings () {
      this.$refs.settings.modal()
    },
    message () {
      this.$msgBox.toggle()
    }
  }
}
</script>
<style>
</style>



